import service_user from '@/http/services/user.js';
import html2canvas from 'html2canvas';
export default {
  name: "Publicity",
  data(){
      return{
        share: false,
        qrcode: '',
        token: '',
        downImg: '',
        downloadText: '生成宣传页',
        hasImg: false
      }
  },
  methods:{
    showShare() {
      this.share = true;
    },
    changeShare(msg) {
      this.share = msg;
    },
    getCookie: function () {
        this.token = this.$cookies.get('access_token');
    },
    getQrcode: function () {
      service_user.userService.getQrcode(this.token).then(res => {
        if (res.status === 200) {
          this.qrcode = res.data;
        }
      })
    },
    toImage: function () {
      if (this.hasImg) {
        return false;
      }
      html2canvas(this.$refs.downloadContent,{
        backgroundColor: null
      }).then((canvas) => {
        let dataURL = canvas.toDataURL("image/png");
        if (dataURL) {
          this.$Message.success('成功生成宣传页');
          this.downImg = dataURL;
          this.downloadText = '长按保存宣传页';
          this.hasImg = true;
        }
      });
    }
  },
 mounted() {
   this.getCookie();
   this.getQrcode();
 },
}



